html, body {
    height: 100%;
}

.innerContent {
    display: -ms-flexbox;
    display: flex;
    margin-top: 70px;
}

.custom-btn-startup {
    width: 550px;
    height: 250px;
    background-color: transparent;
    font-weight: bolder;
    font-size: 80px;
    border: 0px solid transparent;
    border-radius: 60px;
    margin-top: 10%;
}

.custom-btn-startup:hover {
    background-color: rgba(51, 255, 240, 0.6);
}

.icon-startup {
    font-size: 150px;
    color: #ECECEC;
    border-right: 70px solid transparent;
}

.contain {
    height: 100vh;

    text-align: center;
    padding-top: 15%;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;

}

.header {
    font-size: 60px;
    color: white;
    font-weight: bold;
    border-bottom: 80px solid transparent;
}

.content {
    font-size: 20px;
    color: white;
    text-align: center;
}

.custom-nav {
    background-color: #f1f1f1 !important;
}

.custom-nav-brand {
    font-weight: 800;
    color: #636363 !important;
}

.pink {
    background: -webkit-gradient(linear, left top, right top, from(deeppink), to(coral));
    background: -webkit-linear-gradient(left, deeppink 0%, coral 100%);
    background: -o-linear-gradient(left, deeppink 0%, coral 100%);
    background: linear-gradient(to right, deeppink 0%, coral 100%);

}

.teal {
    background: -webkit-gradient(linear, left top, right top, from(SlateBlue), to(DeepSkyBlue));
    background: -webkit-linear-gradient(left, SlateBlue 0%, DeepSkyBlue 100%);
    background: -o-linear-gradient(left, SlateBlue 0%, DeepSkyBlue 100%);
    background: linear-gradient(to right, SlateBlue 0%, DeepSkyBlue 100%);

}

.tomato {
    background: -webkit-gradient(linear, left top, right top, from(tomato), to(gold));
    background: -webkit-linear-gradient(left, tomato 0%, gold 100%);
    background: -o-linear-gradient(left, tomato 0%, gold 100%);
    background: linear-gradient(to right, tomato 0%, gold 100%);

}

.purple {
    background: -webkit-gradient(linear, left top, right top, from(#9966ff), to(#6699ff));
    background: -webkit-linear-gradient(left, #9966ff, #6699ff);
    background: -o-linear-gradient(left, #9966ff, #6699ff);
    background: linear-gradient(to right, #9966ff, #6699ff);
}

.green {
    background: -webkit-gradient(linear, left top, right top, from(#9FFF33), to(#33FFCE));
    background: -webkit-linear-gradient(left, #9FFF33, #33FFCE);
    background: -o-linear-gradient(left, #9FFF33, #33FFCE);
    background: linear-gradient(to right, #9FFF33, #33FFCE);
}


