html, body {
    height: 100%;
    font-size: 30px;
}

.bigContainer-error {
    background: -webkit-gradient(linear, left top, right top, from(#ED213A), to(#93291E));
    background: -webkit-linear-gradient(left, #ED213A, #93291E);
    background: -o-linear-gradient(left, #ED213A, #93291E);
    background: linear-gradient(to right, #ED213A, #93291E);
    height: 100vh;
    color: white;
}

.h1-error {
    padding: 10%;
    font-weight: 700;
}


